import React, {FC} from 'react'
import { makeStyles, Theme } from '@material-ui/core'
import Container from '@material-ui/core/Container'
import Grid from '@material-ui/core/Grid'
//
import MenuContent from './MenuContent'
import HotNewsContent from './HotNewsContent'
import WBContent from './WBContent'

import { homePageMenuItems } from '../../config/menu_config'
import hotNewsData from '../../data/hotNews.json'
import wbData from '../../data/wbContent.json'

//const drawerWidth = 240

const useStyle = makeStyles((theme: Theme) => {
    return {
        gitem: {
            top: '6%',
            position: 'sticky',
            padding: theme.spacing(2),
            borderRadius: theme.shape.borderRadius,
            //backgroundColor: alpha(theme.palette.secondary.main, 0.15),
        },
        gcontainer: {
        },
        gcontent: {
            '@media screen and (max-width: 600px)': {
                marginTop: '12%',
            }
        },
        menuItem: {
            padding: '0.5rem',
        },
        menuItemText: {
            paddingLeft: 0
        },
        hideSM: {
            '@media screen and (max-width: 600px)': {
                display: 'none',
            }
        },
        hideMD: {
            '@media screen and (max-width: 960px)': {
                display: 'none',
            }
        }
    }
})

const HomePage: FC = () => {
    const classes = useStyle()

    return (
        <Container>
            <Grid container spacing={2} className={classes.gcontainer}>
                {/* menu */}
                <Grid item sm={3} className={classes.hideSM}>
                    <MenuContent
                        data={homePageMenuItems}
                    />
                </Grid>
                
                {/* main content */}
                <Grid item container xs={12} sm={9} md={6} spacing={1} className={classes.gcontent}>
                    <WBContent
                        data={wbData.content}
                    />
                </Grid>

                {/* hot news */}
                <Grid item md={3} className={classes.hideMD}>
                    <HotNewsContent
                        custom={classes.gitem}
                        data={hotNewsData.hotNews}
                    />
                </Grid>
            </Grid>
        </Container>
    )
}

export default HomePage
