import React, { FC } from 'react'

import Avatar from '@material-ui/core/Avatar'
import AppBar from '@material-ui/core/AppBar'
import Toolbar from '@material-ui/core/Toolbar'
import IconButton from '@material-ui/core/IconButton'
import Grid from '@material-ui/core/Grid'
import BottomNavigation from '@material-ui/core/BottomNavigation'
import BottomNavigationAction from '@material-ui/core/BottomNavigationAction'
import InputBase from '@material-ui/core/InputBase'
import { makeStyles, Theme } from '@material-ui/core'
import { useHistory, useLocation } from 'react-router-dom'
import ScrollToTop from 'react-scroll-up'
// icons
import HomeIcon from '@material-ui/icons/Home'
import VideoLibraryIcon from '@material-ui/icons/VideoLibrary'
import WhatshotIcon from '@material-ui/icons/Whatshot'
import EmailIcon from '@material-ui/icons/Email'
import GTranslateIcon from '@material-ui/icons/GTranslate'
import SearchIcon from '@material-ui/icons/Search'
import ArrowUpwardIcon from '@material-ui/icons/ArrowUpward'
import { alpha } from '@material-ui/core'

//
import { menuItems } from '../config/menu_config'

const useStyles = makeStyles((theme: Theme) => {
    return {
        /* root style */
        root: {
            display: 'flex',
            height: '100%',
        },
        /* page style */
        page: {
            background: alpha(theme.palette.secondary.light, 0.15),
            width: '100%',
            padding: theme.spacing(2),
        },
        /* navbar style */
        toolbarblock: theme.mixins.toolbar,
        appbar: {
        },
        toolbar: {
            //margin: 'auto 0'
        },
        logoArea: {
            display: 'flex',
        },
        BNAction: {
            backgroundColor: 'transparent',
        },
        search: {
            height: '80%',
            margin: "auto 0",
            position: 'relative',
            borderRadius: theme.shape.borderRadius,
            backgroundColor: alpha(theme.palette.common.white, 0.15),
        },
        searchIcon: {
            padding: theme.spacing(0, 1),
            height: '100%',
            margin: "auto 0",
            position: 'absolute',
            pointerEvents: 'none',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
        },
        inputRoot: {
            color: 'inherit',
            height: '100%',
        },
        inputInput: {
            padding: theme.spacing(1),
            // vertical padding + font size from searchIcon
            paddingLeft: `calc(1em + ${theme.spacing(4)}px)`,
            transition: theme.transitions.create('width'),
            width: '100%',
            [theme.breakpoints.up('sm')]: {
                width: '5rem',
                '&:focus': {
                    width: '8rem',
                },
            },
        },
    }
})

export type LayoutType = {
    children: any
}

const Layout: FC<LayoutType> = ({children}) => {
    const classes = useStyles()
    const history = useHistory()

    return (
        <div className={classes.root}>
            <AppBar color='secondary' className={classes.appbar}>
                <Toolbar variant='dense' className={classes.toolbar}>
                    <Grid container>
                        <Grid item sm={2} className={classes.logoArea}>
                            {/* logo */}
                            <IconButton aria-label="page logo">
                                <GTranslateIcon />
                            </IconButton>
                            {/* search bar */}
                            <div className={classes.search}>
                                <div className={classes.searchIcon}>
                                    <SearchIcon />
                                </div>
                                <InputBase
                                    placeholder="Searchâ€¦"
                                    classes={{
                                        root: classes.inputRoot,
                                        input: classes.inputInput,
                                    }}
                                    inputProps={{ 'aria-label': 'search' }}
                                />
                            </div>
                        </Grid>
                        <Grid item sm={6}>
                            {/* navation area */}
                            <BottomNavigation
                                value={'home'}
                                className={classes.BNAction}
                            >
                                {
                                    menuItems.map((item) => (
                                        <BottomNavigationAction
                                            label={item.title}
                                            icon={item.icon}
                                            onClick={() => history.push(item.path)}
                                        />
                                    ))
                                }
                            </BottomNavigation>

                        </Grid>
                        <Grid item sm={4}>
                            menu placeholder
                        </Grid>

                    </Grid>
                </Toolbar>
            </AppBar>
            {/* content */}
            <div className={classes.page}>
                <div className={classes.toolbarblock}></div>
                {children}
            </div>
            <ScrollToTop showUnder={160} duration={1000}>
                <ArrowUpwardIcon />
            </ScrollToTop>
        </div>
    )
}

export default Layout
