import React, {FC} from 'react'
import Paper from '@material-ui/core/Paper'
import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import ListItemIcon from '@material-ui/core/ListItemIcon'
import ListItemText from '@material-ui/core/ListItemText'
import Typography from '@material-ui/core/Typography'
import Divider from '@material-ui/core/Divider'
import { makeStyles, Theme } from '@material-ui/core'

type MenuContentProps = {
    data: any;
}

const useStyles = makeStyles((theme: Theme) => {
    return {
        root: {
            top: '6%',
            position: 'sticky',
            padding: theme.spacing(2),
            borderRadius: theme.shape.borderRadius,
        },
        hideText: {
            '@media screen and (max-width: 768px)': {
                display: 'none',
            }
        }
    }
})

const MenuContent: FC<MenuContentProps> = ({ data }) => {
    const classes = useStyles()

    const menuContent = data.menuItems.map((item: any) => (
        <ListItem
            key={item.title}
            button
            className={classes.root}
            onClick={() => console.log("click side bar")}
            >
            <ListItemIcon>{item.icon}</ListItemIcon>
            <ListItemText primary={item.title} className={classes.hideText} />

        </ListItem>
    ))
    
    const customContent = data.customization.map((item: any) => (
        <ListItem
            key={item.title}
            button
            className={classes.root}
            onClick={() => console.log("click side bar")}
        >
            <ListItemIcon>{item.icon}</ListItemIcon>
            <ListItemText primary={item.title} className={classes.hideText} />
        </ListItem>
    ))

    return (
        <Paper className={classes.root}>
            <Typography variant='h5' gutterBottom align='left'>
                {data.title.text}
            </Typography>
            <List>
                {menuContent}
            </List>
            <Divider />
            <List>
                {customContent}
            </List>
        </Paper>
    )
}

export default MenuContent
