import React, {FC} from 'react'
import Avatar from '@material-ui/core/Avatar'
import Button from '@material-ui/core/Button'
import Container from '@material-ui/core/Container';
import Card from '@material-ui/core/Card'
import CardHeader from '@material-ui/core/CardHeader';
import CardMedia from '@material-ui/core/CardMedia';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid'
import { makeStyles, Theme } from '@material-ui/core';
// icons
import FilterNoneIcon from '@material-ui/icons/FilterNone'
import ThumbUpAltIcon from '@material-ui/icons/ThumbUpAlt'
import MessageIcon from '@material-ui/icons/Message'

type WBContentProps = {
    data: any;
}

const useStyles = makeStyles((theme: Theme) => {
    return {
        root: {
            padding: theme.spacing(2),
            borderRadius: theme.shape.borderRadius,
        },
        cardContent: {
            marginLeft: '3rem',
        },
        cardMedia: {
            height: 0,
            paddingTop: '56.25%',
            margin: '0 1rem 1rem 4rem',
        },
        cardActions: {
            justifyContent: 'space-around'
        }
    }

})

const WBContent: FC<WBContentProps> = ({ data }) => {
    const classes = useStyles()
    
    const content = data.map((item: any )=> (
        <Grid item className={classes.root} xs={12}>        
            <Card>
                <CardHeader
                    avatar={
                        <Avatar>{item.blogger[0].toUpperCase()}</Avatar>                    
                    }
                    title={item.blogger}
                    subheader={item.date}
                >
                </CardHeader>

                {/* text content */}
                {item.wb ?
                <CardContent className={classes.cardContent}>
                        <Typography variant='body1' color='textSecondary' component="p">
                            {item.wb}
                        </Typography>
                    </CardContent>
                    : null
                }
                
                {/* image media */}
                {item.img ?
                    <CardMedia
                        className={classes.cardMedia}
                        image={item.img}
                    />
                    : null
                }

                {/* bottom */}
                <CardActions className={classes.cardActions}>
                    <Button size="small" color="secondary" startIcon={<FilterNoneIcon />} >
                        Repost
                    </Button>
                    <Button size="small" color="secondary"startIcon={<MessageIcon />} >
                        Comment
                    </Button>
                    <Button size="small" color="secondary" startIcon={<ThumbUpAltIcon />} >
                        {item.likes}
                    </Button>
                </CardActions>
            </Card>
        </Grid>
    ))

    return (
        <>
            {content}
        </>
    )
}

export default WBContent
